import Clause from './Clause';
import "babel-polyfill";
import { ITag } from 'office-ui-fabric-react';
import * as _ from 'underscore';
import { number } from 'prop-types';
import * as Utils from '../components/Utils';

const lnClauses = require('../data/LNClausesAndDefinitions.json');
const lnTags = require('../data/PATags.json');

export default class Clauses extends Array<Clause>{
    
    lastUpdatedOn: number;
    
    constructor() {
        super();
        
    }

    isOutdated = ()=> {
        const upToDate = Clauses.getLastUpdatedOnFromLocalStorage();
        
        if(upToDate==null) return false;

        return this.lastUpdatedOn !== upToDate;
    }

    public static showLNClauses(): boolean {
        let showClauses = localStorage.getItem("showLNClauses");
        if(showClauses==undefined){
            localStorage.setItem("showLNClauses", JSON.stringify(true));
            return true;
        }
        else{
            return JSON.parse(showClauses);
        }
    }

    public static setShowLNClauses(flag: boolean){
        localStorage.setItem("showLNClauses", JSON.stringify(flag));
    }

    public static getClausesFromLocalStorage() : Clauses{
        var clauses = Object.assign(new Clauses, JSON.parse(localStorage.getItem("clauses")) || "");
        clauses.lastUpdatedOn = Clauses.getLastUpdatedOnFromLocalStorage();
        
        //clauses = Object.assign(new Clauses, lnClauses.map(a=>new Clause(a.id, a.title, a.html, true, a.tags)));
        if(Clauses.showLNClauses()){
            clauses = Object.assign(new Clauses, 
            clauses.concat(lnClauses.map(a=>new Clause(a.id, a.title, a.html, a.draftingNotes, true, a.tags))));
        }

        return clauses.sort((a, b)=>{return a.title.toLowerCase() >= b.title.toLowerCase() ? 1 : -1});
    }

    public static getTagsFromLocalStorage() : ITag[]{
         let tags = JSON.parse(localStorage.getItem("tags") || "[]");
         
         if(Clauses.showLNClauses()){
            tags = tags.concat(lnTags);
         }

         return tags.map(item => ({ key: item, name: item, isNewItem: false }));
    }

    /**
     * insertOrUpdate
c: Clause  : void    */
    insertOrUpdate = (c: Clause) : void => {
        const index = this.findIndex(a=> a.id === c.id);

        if(index<0){
            this.push(c);
        }
        else{
            this[index] = c;
        }
    }

    remove = (c: Clause) : void => {
        //debugger;
        const index = this.findIndex(a=> a.id === c.id);

        if(index>=0){
            this.splice(index,1);
        }

        this.saveToLocalStorage();
    }

    saveToLocalStorage = ():void => {

        //not sure why this does not work... ???
        //console.log(_.omit(this, ["isExpanded"]));
        //debugger;
        localStorage.setItem("clauses", JSON.stringify(this.filter(c=>!c.isReadOnly)));

        console.log("clauses saved...");
        console.log(this);

        //save tags too         
        //use set to ensure distinct      
        const allTags = new Set([].concat.apply([], this.map(c=>c.tags.map(t=>t.name))));

        localStorage.setItem("tags", JSON.stringify(Array.from(allTags).filter(t=>!lnTags.includes(t))));

        //timestamp update
        localStorage.setItem("lastUpdatedOn", new Date().getTime().toString());
    }

    filterDown = (filterText: string, filterTags: ITag[]) : Clause[] => {

        // if((!filterText || filterText.length  === 0) && (!filterTags || filterTags.length === 0)){
        //     return this;
        // }
        
        if(!filterText){
            filterText = "";
        }

        if(!filterTags){
            filterTags = [];
        }
        

        //const filterTerms = filterText.trim().split(" ");
        const filterRegex =new RegExp(filterText.trim().replace(/\s+/, "|"), "i");
        
        // return this.filter(a=>
        //     (filterText.length == 0 || (a.title + " " + Utils.htmlToPlain(a.html)).search(filterRegex) >= 0) &&
        //     (filterTags.length == 0 || a.tags.some(t=> filterTags.some(f=>f.key == t.key)))
            
        //     );

        return this.filter(a=>
            (filterText.length == 0 || a.title.search(filterRegex) >= 0) &&
            (filterTags.length == 0 || filterTags.every(t=>a.tags.some(f=>f.key == t.key))) //a.tags.some(t=> filterTags.some(f=>f.key == t.key)))
            ).sort((a, b)=>{return a.title.toLowerCase() >= b.title.toLowerCase() ? 1 : -1});
        
    }

    public static getLastUpdatedOnFromLocalStorage() : number{
        const lastUpdatedOn = localStorage.getItem("lastUpdatedOn");
        return lastUpdatedOn ? +lastUpdatedOn : null;
    }
}