import Clauses from "../models/Clauses";

Office.onReady().then(() => console.log("officejs is ready"));

export default class DisplayGrabberDialog{
    public static ShowDialog(id: string, customMessageHandler?: (arg) => void){
        
        const dialog = new DisplayGrabberDialog();

        dialog.customMessageHandler = customMessageHandler;
        
        //IE does not support baseURI
        Office.context.ui.displayDialogAsync((document.baseURI || document.getElementsByTagName('base')[0].href)+'/grabber.html?ssId=' + id, 
        { height: 60, width: 60, displayInIframe: true }, dialog.dialogCallback);
    }

    customMessageHandler: any;
    dialog: Office.Dialog;

    dialogCallback = (asyncResult: any) => {
        if (asyncResult.status == "failed") {

            // In addition to general system errors, there are 3 specific errors for 
            // displayDialogAsync that you can handle individually.
            switch (asyncResult.error.code) {
                case 12004:
                    console.log("Domain is not trusted");
                    break;
                case 12005:
                    console.log("HTTPS is required");
                    break;
                case 12007:
                    console.log("A dialog is already opened.");
                    break;
                default:
                    console.log(asyncResult.error.message);
                    break;
            }
        }
        else {
            this.dialog = asyncResult.value;
            
            
            /*Messages are sent by developers programatically from the dialog using office.context.ui.messageParent(...)*/
            this.dialog.addEventHandler(Office.EventType.DialogMessageReceived, this.messageHandler);

            /*Events are sent by the platform in response to user actions or errors. For example, the dialog is closed via the 'x' button*/
            this.dialog.addEventHandler(Office.EventType.DialogEventReceived, this.eventHandler);
        }
    }

    messageHandler = (arg) => {

        try{
            const retVal = arg.message;
            console.log(retVal);

            if(retVal){
                console.log("saving...");

                let clauses: Clauses = Clauses.getClausesFromLocalStorage();

                console.log(retVal);
                clauses.insertOrUpdate(JSON.parse(retVal));

                clauses.saveToLocalStorage();
            }

            if(this.customMessageHandler){
               this.customMessageHandler(arg);
            }
        }
        finally{
            this.dialog.close();
        }
    }

    eventHandler = (arg) => {

        // In addition to general system errors, there are 2 specific errors 
        // and one event that you can handle individually.
        switch (arg.error) {
            case 12002:
                console.log("Cannot load URL, no such page or bad URL syntax.");
                break;
            case 12003:
                console.log("HTTPS is required.");
                break;
            case 12006:
                // The dialog was closed, typically because the user the pressed X button.
                console.log("Dialog closed by user");
                break;
            default:
                console.log("Undefined error in dialog window");
                break;
        }
    }
}