//import Office from '@microsoft/office-js';
import uuidv4 from 'uuid/v4';
import DisplayGrabberDialog from '../src/components/DisplayGrabberDialog';
import { polyfill } from 'es6-promise'; 
polyfill();

(() => {
  // The initialize function must be run each time a new page is loaded
  Office.initialize = () => {
    console.log("function file loaded...!");
  };

  (window as any).grabClause = async (event: any) => {
    console.log("i also get invoked...");

    const id = uuidv4();

    return Word.run(async context => {

      var range = context.document.getSelection();
      let htmlObj = range.getHtml();

      await context.sync();

      console.log(id);
      console.log(htmlObj.value);

      localStorage.setItem(id, JSON.stringify({html: htmlObj.value}));

    }).then(() => DisplayGrabberDialog.ShowDialog(id))      
      .finally(() => event.completed());
  }

})();